
.home_info_item{
    margin: 50px 0;
    text-align: end;
    h1{
        font-size: 50px;
        font-weight: 800;
    }
    h3{
        font-size: 20px;
        word-wrap: break-word;
        color: #868490;
    }
}

.effect_img{
    position: absolute;
    z-index: -2;
}
.effect_img:first-child{
    top: 0;
    right: 0;
}
.effect_img:nth-child(2){
    top: 40vh;
    left: 0;
}
.effect_img:nth-child(3){
    top: 70vh;
    right: 0;
}
#links{
    position: relative;
    .links{
        position: relative;
        z-index: 1;
        padding: 40px 0;
    }
    .links_effect{
        position: absolute;
        background: #0b133c;
        width: 100%;
        height: 100%;
        z-index: 1;
        opacity: .8;
        filter: blur(3px);
        -webkit-filter: blur(3px);
}
    .links_item{
        font-size: 25px;
        color: #868490;
    }
}

.services{
    .services_item{
        padding: 15px 20px;
        border: 1px solid #08103a;
        background: #0b133c;
        box-shadow: 0 0 15px 5px #0b133c;
        margin: 10px 0;
    }
}
